import * as React from 'react';
import styled from 'styled-components';

import type { MobileToolbarButtonProps } from '@ali/4ever-component';
import { useDefaultSz } from '@ali/4ever-component';
import { setSz } from '../actions';
import { szList } from '@ali/4ever-plugin-sz';

const SzWrapper = styled.div`
  flex: 0.8;
  margin-right: 1px;
  margin-bottom: 12px;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const Icon = styled.img`
  width: 20px;
  height: 20px;
`;

const Line1 = styled.div`
  font-size: 12px;
  color: #171a1d;
  line-height: 16px;
`;
const Line2 = styled.div`
  font-size: 17px;
  font-weight: 500;
  color: #171a1d;
  line-height: 20px;
`;

export function MobileSizeInc(props: MobileToolbarButtonProps) {
  const { controller, item, Wrapper } = props;
  const defaultSz = useDefaultSz();
  const sz = controller.query('getSize', defaultSz);

  const handleClick = React.useCallback(() => {
    const newSize = szList.find((s) => s > sz) || szList[szList.length - 1];
    controller.run('onAction', setSz(newSize));
  }, [controller, sz]);

  return (
    <Wrapper type="horiz" item={item} onClick={handleClick} icon="sizePlus" />
  );
}

export function MobileSizeDec(props: MobileToolbarButtonProps) {
  const { controller, item, Wrapper } = props;
  const defaultSz = useDefaultSz();
  const sz = controller.query('getSize', defaultSz);

  const handleClick = React.useCallback(() => {
    const newSize = [...szList].reverse().find((s) => s < sz) || szList[0];
    controller.run('onAction', setSz(newSize));
  }, [controller, sz]);

  return (
    <Wrapper type="horiz" item={item} onClick={handleClick} icon="sizeMinus" />
  );
}

export function MobileSizeNow(props: MobileToolbarButtonProps) {
  const { controller, locale } = props;
  const defaultSz = useDefaultSz();
  const sz = controller.query('getSize', defaultSz);

  return (
    <SzWrapper>
      <Line1>{locale?.titleMobile}</Line1>
      <Line2>{sz}</Line2>
    </SzWrapper>
  );
}
